'use server';
/**
 * @fileOverview AI Chatbot flow for Nextgen-Ecourt.
 *
 * - askChatbot - A function that handles user queries to the chatbot.
 * - ChatbotInput - The input type for the askChatbot function.
 * - ChatbotOutput - The return type for the askChatbot function.
 */

import { ai } from '@/ai/genkit';
import type { PartData } from 'genkit/ai'; // Correct Genkit v1.x import for PartData
import {
  ChatbotInputSchema,
  ChatbotOutputSchema,
  type ChatbotInput,
  type ChatbotOutput,
} from '@/ai/schemas/chatbot';

export type { ChatbotInput, ChatbotOutput };

export async function askChatbot(
  input: ChatbotInput
): Promise<ChatbotOutput> {
  return chatbotFlow(input);
}

// System instructions that guide the AI chatbot's responses
const systemInstruction = `You are an AI Legal Assistant for Nextgen-Ecourt, a digital judiciary management platform.
Your role is to assist users (lawyers, plaintiffs, defendants, judges, court officials) with their queries.
Be polite, professional, and provide clear, concise information.

You can help with the following:
- Answering questions about case status (e.g., "What is the status of my case ABC123?")
- Providing hearing dates (e.g., "When is the next hearing for case XYZ?")
- Explaining legal terms (e.g., "What does 'summon' mean in court?")
- Guiding on document uploads (e.g., "How can I upload documents for my case?")
- Summarizing a case if details are provided (e.g., "Give me a summary of case DEF456. It involves...")
- Explaining court processes (e.g., "What is the next step after filing a case?")

If you need specific information like a case number to answer a question, politely ask the user to provide it.
If a query is outside your scope of legal/court assistance, politely state that you cannot help with that topic.
Do not provide legal advice, only legal information and procedural guidance relevant to the Nextgen-Ecourt platform.
Use the provided chat history and case context to maintain context in the conversation.`;

interface GeminiContentPart {
  text: string;
}
interface GeminiContent {
  role: 'user' | 'model';
  parts: GeminiContentPart[];
}

// Define the chatbot flow to process user input and interact with AI
const chatbotFlow = ai.defineFlow(
  {
    name: 'chatbotFlow',
    inputSchema: ChatbotInputSchema,
    outputSchema: ChatbotOutputSchema,
  },
  async (input: ChatbotInput): Promise<ChatbotOutput> => {
    const processedContents: GeminiContent[] = [];
    
    // Start with the system instruction as the first part of the user's message
    // or as a dedicated system message if supported by the model/Genkit plugin.
    // For Gemini, typically you prepend system instructions to the first user message.
    let currentMessageText = systemInstruction;
    currentMessageText += "\n\nNow start responding to this:\n\n";

    // Add case context if available
    if (input.caseContext) {
      currentMessageText += `Relevant Case Context: ${input.caseContext}\n\n`;
    }

    // Add chat history
    if (input.chatHistory && input.chatHistory.length > 0) {
      input.chatHistory.forEach(msg => {
        // Skip the initial bot greeting if it's the very first in actual history
        if (
          msg.role === 'model' &&
          msg.parts[0]?.text?.includes("Hello! I'm the Nextgen-Ecourt Legal Assistant") &&
          processedContents.length === 0 // only if it's truly the first message being processed
        ) {
          // Do nothing, skip this message
        } else {
          const validParts = msg.parts
            .map(p => ({ text: p.text || '' }))
            .filter(p => p.text.trim());
          if (validParts.length > 0) {
            processedContents.push({ role: msg.role, parts: validParts });
          }
        }
      });
    }
    
    // Append the current user query to the ongoing message text
    currentMessageText += input.userQuery;

    // Add the final compiled user message to contents
    processedContents.push({ role: 'user', parts: [{ text: currentMessageText.trim() }] });
    
    // Ensure there's at least one message, even if it's just the system prompt + "Hello"
    if (processedContents.length === 0 || (processedContents.length === 1 && processedContents[0].role === 'user' && !processedContents[0].parts[0]?.text?.trim())) {
       // This should not happen if input.userQuery is guaranteed to be non-empty
       // and systemInstruction is present. As a fallback:
       processedContents.length = 0; // Clear potentially malformed content
       processedContents.push({ role: 'user', parts: [{ text: systemInstruction + "\n\nNow start responding to this:\n\nHello" }] });
    }


    // Generate the AI response with the appropriate structure
    const llmResponse = await ai.generate({
      messages: processedContents, // Pass the array of GeminiContent
    });

    const responseText = llmResponse.text; // Access .text directly on the response object
    if (responseText === undefined || responseText === null) {
      throw new Error('AI model did not return a text response.');
    }
    return { response: responseText };
  }
);
