'use client';

import { useState, useEffect } from 'react';
import { usePathname, useSearchParams } from 'next/navigation';
import ChatbotButton from '@/components/chatbot/ChatbotButton';
import ChatbotModal from '@/components/chatbot/ChatbotModal';
import { mockCases } from '@/data/mockData'; // For fetching case details for context

export default function ChatbotIntegration() {
  const [isChatbotModalOpen, setIsChatbotModalOpen] = useState(false);
  const [currentCaseContext, setCurrentCaseContext] = useState<string | undefined>(undefined);
  
  const pathname = usePathname();
  const searchParams = useSearchParams();

  useEffect(() => {
    let context: string | undefined;
    if (pathname.startsWith('/cases/')) {
      const caseId = pathname.split('/')[2];
      if (caseId) {
        const foundCase = mockCases.find(c => c.id === caseId);
        if (foundCase) {
          context = `Currently viewing Case: ${foundCase.caseNumber} - ${foundCase.title}. Status: ${foundCase.status}.`;
        }
      }
    } else if (pathname.startsWith('/summaries') || pathname.startsWith('/legal-research')) {
        const caseId = searchParams.get('caseId');
         if (caseId) {
            const foundCase = mockCases.find(c => c.id === caseId);
            if (foundCase) {
                context = `Context from page: User is on ${pathname.substring(1)} page, related to Case: ${foundCase.caseNumber} - ${foundCase.title}.`;
            } else {
                context = `Context from page: User is on ${pathname.substring(1)} page, for case ID ${caseId} (details not found).`;
            }
        } else {
           context = `Context from page: User is on ${pathname.substring(1)} page.`;
        }
    }
    setCurrentCaseContext(context);
  }, [pathname, searchParams]);


  return (
    <>
      <ChatbotButton onClick={() => setIsChatbotModalOpen(true)} />
      <ChatbotModal
        isOpen={isChatbotModalOpen}
        onOpenChange={setIsChatbotModalOpen}
        caseContext={currentCaseContext}
      />
    </>
  );
}
