'use client';

import type React from 'react';
import { useState, useRef, useEffect } from 'react';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogFooter,
} from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Loader2, Send, User, Bot, AlertCircle } from 'lucide-react';
import { askChatbot, type ChatbotInput } from '@/ai/flows/chatbot-flow';
import type { FrontendMessagePart } from '@/ai/schemas/chatbot'; // Use specific frontend type

interface ChatMessage {
  id: string;
  role: 'user' | 'model';
  parts: FrontendMessagePart[]; 
}

interface ChatbotModalProps {
  isOpen: boolean;
  onOpenChange: (isOpen: boolean) => void;
  caseContext?: string;
}

const ChatbotModal: React.FC<ChatbotModalProps> = ({ isOpen, onOpenChange, caseContext }) => {
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [inputValue, setInputValue] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const scrollAreaViewportRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (scrollAreaViewportRef.current) {
      scrollAreaViewportRef.current.scrollTo({ top: scrollAreaViewportRef.current.scrollHeight, behavior: 'smooth' });
    }
  }, [messages]);

  const handleSubmit = async (e?: React.FormEvent<HTMLFormElement>) => {
    e?.preventDefault();
    if (!inputValue.trim()) return;

    const userMessageText = inputValue;
    const userMessage: ChatMessage = {
      id: Date.now().toString() + '-user',
      role: 'user',
      parts: [{ text: userMessageText }],
    };
    setMessages((prev) => [...prev, userMessage]);
    setInputValue('');
    setIsLoading(true);
    setError(null);

    try {
      // Prepare chat history for the API, excluding the current user message which is part of userQuery
      const chatHistoryForAPI = messages.map(msg => ({
        role: msg.role,
        parts: msg.parts.map(p => ({ text: p.text || '' })) 
      }));

      const input: ChatbotInput = {
        userQuery: userMessageText,
        caseContext: caseContext,
        chatHistory: chatHistoryForAPI,
      };
      const result = await askChatbot(input);
      const botMessage: ChatMessage = {
        id: Date.now().toString() + '-bot',
        role: 'model',
        parts: [{ text: result.response }],
      };
      setMessages((prev) => [...prev, botMessage]);
    } catch (err) {
      console.error('Chatbot error:', err);
      const errorMessageText = err instanceof Error ? err.message : 'An unexpected error occurred.';
      setError(errorMessageText); // Set error for display
      const errorMessage: ChatMessage = {
        id: Date.now().toString() + '-error',
        role: 'model',
        parts: [{ text: `Sorry, I encountered an error: ${errorMessageText}. Please try again.` }],
      };
      setMessages((prev) => [...prev, errorMessage]);
    } finally {
      setIsLoading(false);
    }
  };
  
  useEffect(() => {
    if (isOpen && messages.length === 0) {
      setMessages([
        {
          id: 'initial-bot-message',
          role: 'model',
          parts: [{text: "Hello! I'm the Nextgen-Ecourt Legal Assistant. How can I help you today?"}]
        }
      ]);
      setError(null); // Clear any previous errors when reopening with no messages
    }
  }, [isOpen, messages.length]);


  return (
    <Dialog open={isOpen} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-[450px] md:max-w-[600px] lg:max-w-[500px] flex flex-col h-[70vh] max-h-[600px] p-0">
        <DialogHeader className="p-6 pb-3 border-b">
          <DialogTitle className="text-lg font-semibold flex items-center">
            <Bot className="mr-2 h-6 w-6 text-primary" />
            Nextgen-Ecourt Assistant
          </DialogTitle>
          <DialogDescription className="text-sm">Ask me anything about your cases or court procedures.</DialogDescription>
        </DialogHeader>
        
        <ScrollArea className="flex-grow px-6 pt-4 pb-2 overflow-y-auto" viewportRef={scrollAreaViewportRef}>
          <div className="space-y-4">
            {messages.map((message) => (
              <div
                key={message.id}
                className={`flex ${
                  message.role === 'user' ? 'justify-end' : 'justify-start'
                }`}
              >
                <div
                  className={`flex items-start max-w-[85%] rounded-lg px-3 py-2 shadow-md ${
                    message.role === 'user'
                      ? 'bg-primary text-primary-foreground'
                      : 'bg-muted text-foreground' 
                  } ${message.id === 'initial-bot-message' ? 'bg-secondary text-secondary-foreground' : ''}
                  ${message.id.endsWith('-error') ? '!bg-destructive !text-destructive-foreground' : ''}
                  `}
                >
                  {message.role === 'model' && <Bot className="h-5 w-5 mr-2 flex-shrink-0 mt-0.5" />}
                  <div className="whitespace-pre-wrap break-words">
                    {message.parts.map((part, index) => <span key={index}>{part.text}</span>)}
                  </div>
                  {message.role === 'user' && <User className="h-5 w-5 ml-2 flex-shrink-0 mt-0.5" />}
                </div>
              </div>
            ))}
            {isLoading && (
              <div className="flex justify-start">
                <div className="flex items-start max-w-[85%] rounded-lg px-3 py-2 shadow-md bg-muted text-foreground">
                  <Bot className="h-5 w-5 mr-2 flex-shrink-0 mt-0.5" />
                  <Loader2 className="h-5 w-5 animate-spin" /> 
                  <span className="ml-2">Typing...</span>
                </div>
              </div>
            )}
          </div>
        </ScrollArea>

        <DialogFooter className="p-4 pt-2 border-t">
          <form onSubmit={handleSubmit} className="flex w-full items-center space-x-2">
            <Input
              value={inputValue}
              onChange={(e) => setInputValue(e.target.value)}
              placeholder="Type your message..."
              className="flex-grow"
              disabled={isLoading}
              aria-label="Chat message input"
            />
            <Button type="submit" size="icon" disabled={isLoading || !inputValue.trim()} aria-label="Send message">
              {isLoading ? <Loader2 className="h-5 w-5 animate-spin" /> : <Send className="h-5 w-5" />}
            </Button>
          </form>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
};

export default ChatbotModal;
