'use client';

import Link from 'next/link';
import NextgenEcourtLogo from '@/components/icons/NextgenEcourtLogo'; // Updated import
import UserProfile from '@/components/auth/UserProfile';
import { Button } from '@/components/ui/button';
import { useAuth } from '@/contexts/AuthContext';
import { Sheet, SheetContent, SheetTrigger } from '@/components/ui/sheet';
import { Menu } from 'lucide-react';
import { usePathname } from 'next/navigation';
import { cn } from '@/lib/utils';

const navItems = [
  { href: '/', label: 'Dashboard' },
  { href: '/summaries', label: 'AI Summaries' },
  { href: '/legal-research', label: 'Legal Research' },
  // Add Judge Dashboard link conditionally
];

export default function Header() {
  const { user } = useAuth();
  const pathname = usePathname();

  const currentNavItems = [...navItems];
  if (user?.role === 'Judge') {
    // Check if Judge Dashboard already exists to avoid duplicates if this runs multiple times
    if (!currentNavItems.find(item => item.href === '/judge-dashboard')) {
      currentNavItems.push({ href: '/judge-dashboard', label: 'Judge Dashboard' });
    }
  }


  return (
    <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <div className="container flex h-16 items-center justify-between">
        <Link href="/" className="mr-6 flex items-center space-x-2" aria-label="Nextgen-Ecourt Home">
          <NextgenEcourtLogo /> {/* Updated component */}
        </Link>
        
        <nav className="hidden md:flex items-center space-x-6 text-sm font-medium">
          {currentNavItems.map((item) => (
            <Link
              key={item.href}
              href={item.href}
              className={cn(
                "transition-colors hover:text-primary",
                pathname === item.href ? "text-primary font-semibold" : "text-muted-foreground"
              )}
            >
              {item.label}
            </Link>
          ))}
        </nav>

        <div className="flex items-center gap-4">
          <UserProfile />
          <Sheet>
            <SheetTrigger asChild>
              <Button
                variant="outline"
                size="icon"
                className="shrink-0 md:hidden"
                aria-label="Toggle navigation menu"
              >
                <Menu className="h-5 w-5" />
                <span className="sr-only">Toggle navigation menu</span>
              </Button>
            </SheetTrigger>
            <SheetContent side="left" className="w-[280px] sm:w-[320px]">
              <nav className="grid gap-6 text-lg font-medium pt-4">
                <Link
                  href="/"
                  className="flex items-center gap-2 text-lg font-semibold mb-4"
                  aria-label="Nextgen-Ecourt Home"
                >
                  <NextgenEcourtLogo /> {/* Updated component */}
                  <span className="sr-only">Nextgen-Ecourt</span> {/* Updated sr-only text */}
                </Link>
                {currentNavItems.map((item) => (
                  <Link
                    key={item.href}
                    href={item.href}
                     className={cn(
                      "transition-colors hover:text-primary py-2",
                      pathname === item.href ? "text-primary font-semibold" : "text-muted-foreground"
                    )}
                  >
                    {item.label}
                  </Link>
                ))}
              </nav>
            </SheetContent>
          </Sheet>
        </div>
      </div>
    </header>
  );
}
